import React from 'react';
import './ChangepasswordContainer.css';
import { connect } from 'react-redux';

import { changePassword } from '../../../actions/userActions';
import { hidepopupLayer } from '../../../actions/popupLayerActions';
class ChangePasswordContainer extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			oldPassword: '',
			newPassword: '',
			errMsg: '',
		};
		this.handleChangepassword = this.handleChangepassword.bind(this);
	}

	handleChangepassword() {
		let { changePassword } = this.props;
		let { oldPassword, newPassword } = this.state;
		if (oldPassword && newPassword) {
			changePassword(oldPassword, newPassword);
		} else {
			this.setState({ errMsg: 'All fileds are mandatory' });
		}
	}

	render() {
		let { errMessage } = this.props;
		return (
			<div className="dflex flexcolumn cpFormContainer h100">
				<div className="flexgrow formElements">
					<div className="form-control">
						<label className={this.state.oldPassword.length > 0 ? '' : 'required'}>Old Password</label>
						<input
							type="text"
							className="form-input"
							value={this.state.oldPassword}
							onChange={(e) => {
								this.setState({ oldPassword: e.target.value, errMsg: '' });
							}}
						/>
					</div>
					<div className="form-control">
						<label className={this.state.newPassword.length > 0 ? '' : 'required'}>New Password</label>
						<input
							type="text"
							className="form-input"
							value={this.state.newPassword}
							onChange={(e) => {
								this.setState({ newPassword: e.target.value, errMsg: '' });
							}}
						/>
					</div>
					{errMessage ? <div className="required">{errMessage}</div> : null}
					{this.state.errMsg ? <div className="required">{this.state.errMsg}</div> : null}
				</div>
				<div className="formFooter">
					<span className="formBtn" onClick={this.handleChangepassword}>
						Change Password
					</span>
				</div>
			</div>
		);
	}
}

function mapStateToProps(state, props) {
	return {
		errMessage: state.user.errMessage,
	};
}

export default connect(mapStateToProps, { changePassword, hidepopupLayer })(ChangePasswordContainer);
