export function approveTestimonial(data) {
	return (dispath) => {
		fetch(`/testimonials/${data.id}`, {
			method: 'PATCH',
			body: JSON.stringify({
				approvalStatus: data.approvalStatus,
			}),
			headers: {
				authorization: `Bearer ${localStorage.getItem('token')}`,
				'Content-Type': 'application/json',
			},
		})
			.then((res) => {
				if (res.status === 401) {
					dispath({
						type: 'LOGOUT_USER',
					});
					throw new Error('UN_AUTHORIZED');
				}
				return res.json();
			})
			.then((res) => {
				dispath({
					type: 'APPROVE_TESTIMONIAL',
					data: {
						id: res.data.id,
						isApproved: res.data.isApproved,
					},
				});
			})
			.catch((err) => {});
	};
}

function normalize(array, key) {
	return array.reduce((obj, item) => {
		let key1 = item[key];
		obj[key1] = item;
		return obj;
	}, {});
}

export function getTestimonials() {
	return (dispath) => {
		fetch('/testimonials', {
			method: 'GET',
			headers: {
				authorization: `Bearer ${localStorage.getItem('token')}`,
			},
		})
			.then((res) => {
				if (res.status === 204) {
					return { data: [] };
				}
				if (res.status === 401) {
					dispath({
						type: 'LOGOUT_USER',
					});
					throw new Error('UnAUTHORIZED');
				}

				return res.json();
			})
			.then((res) => {
				dispath({
					type: 'GET_TESTIMONIALS',
					data: {
						testimonials: normalize(res.data, 'id'),
					},
				});
			});
	};
}
