export default function projectsReducer(state = {}, action) {
	switch (action.type) {
		case 'GET_PROJECTS':
			console.log(action.data.projects);
			return Object.assign({}, state, action.data.projects);
		case 'ADD_PROJECT':
			return Object.assign({}, state, { [action.data.id]: action.data });
		case 'UPDATE_PROJECT':
			return Object.assign({}, state, { [action.data.id]: action.data.data });
		case 'DELETE_PROJECT':
			delete state[action.data.projectId];
			return Object.assign({}, state);
		default:
			return state;
	}
}
