import React from 'react';
import './TestimonialsContainer.css';

import List from '../../../components/admin/list/List';
import Chip from '../../../components/admin/chip/Chip';
import { connect } from 'react-redux';

import { approveTestimonial, getTestimonials } from '../../../actions/testimonialActions';

class TestimonialContainer extends React.Component {
	componentDidMount() {
		this.props.getTestimonials();
	}
	render() {
		let { testimonials, approveTestimonial } = this.props;
		return (
			<React.Fragment>
				<List title="Testimonials" isAddIconNeeded={false}>
					{Object.keys(testimonials).map((key) => {
						let { feedback, company, designation, name, isApproved } = testimonials[key];
						return (
							<Testimonials
								key={key}
								testimonialId={key}
								feedback={feedback}
								company={company}
								name={name}
								designation={designation}
								isApproved={isApproved}
								approveHandler={(testimonialId) => {
									let testimonial = testimonials[testimonialId];
									approveTestimonial({
										id: testimonialId,
										approvalStatus: !testimonial.isApproved,
									});
								}}
							/>
						);
					})}
				</List>
			</React.Fragment>
		);
	}
}

function Testimonials(props) {
	let { testimonialId, approveHandler, company, designation, name, feedback, isApproved } = props;
	return (
		<div className="dflex testimonialContainer">
			<div className="flexgrow">
				<div className="dflex flexcolumn">
					<div>{feedback}</div>
					<div className="testimonialUser">
						<div className="dflex">
							<div>
								<Chip name={name} color="grey" />
							</div>
							<div>
								<Chip name={designation} color="grey" />
							</div>
							<div>
								<Chip name={company} color="grey" />
							</div>
						</div>
					</div>
				</div>
			</div>
			<div>
				<div
					className="testimonialAction pointer"
					onClick={() => {
						console.log('clicked', testimonialId);
						approveHandler(testimonialId);
					}}>
					<Chip name={isApproved ? 'Reject' : 'Approve'} color={isApproved ? 'red' : 'green'} />
				</div>
			</div>
		</div>
	);
}

function mapStateToProps(state, props) {
	return {
		testimonials: state.testimonials,
	};
}

export default connect(mapStateToProps, { approveTestimonial, getTestimonials })(TestimonialContainer);
