export function login(userObj) {
	return (dispatch) => {
		fetch('/user/signin', {
			method: 'POST',
			headers: { 'Content-Type': 'application/json' },
			body: JSON.stringify({ email: userObj.email, password: userObj.password }),
		})
			.then((res) => {
				if (res.status === 401) {
					throw { name: 'UNAUTHORIZED', message: 'Invalid username / password' };
				}
				return res.json();
			})
			.then((res) => {
				localStorage.setItem('token', res.token);
				dispatch({
					type: 'LOGIN_USER',
				});
			})
			.catch((err) => {
				dispatch({
					type: 'LOGIN_FAILED',
					data: {
						errMessage: err.message,
					},
				});
			});
	};
}

export function logout() {
	return (dispatch) => {
		localStorage.removeItem('token');
		dispatch({
			type: 'LOGOUT_USER',
		});
	};
}

export function changePassword(oldPassword, newPassword) {
	return (dispatch) => {
		fetch('/user/changepassword', {
			method: 'PATCH',
			headers: {
				'Content-Type': 'application/json',
				authorization: `Bearer ${localStorage.getItem('token')}`,
			},
			body: JSON.stringify({
				oldPassword,
				newPassword,
			}),
		})
			.then((res) => {
				console.log(res);
				if (res.ok) {
					return res.json();
				} else {
					throw 'PASSWORD UPDATE FAILED';
				}
			})
			.then((res) => {
				dispatch({
					type: 'HIDE_POPUP',
				});
				dispatch({
					type: 'CHANGE_PASSWORD_SUCCESS',
				});
			})
			.catch((err) => {
				dispatch({
					type: 'CHANGE_PASSWORD_FAILED',
					data: err,
				});
			});
	};
}
