function normalize(array, key) {
	return array.reduce((obj, item) => {
		let key1 = item[key];
		obj[key1] = item;
		return obj;
	}, {});
}

export function addProject(data) {
	let formData = new FormData();
	formData.append('title', data.title);
	formData.append('section1', data.section1);
	formData.append('section2', data.section2);
	for (const key of Object.keys(data.images)) {
		console.log();
		formData.append('images', data.images[key]);
	}
	return (dispatch) => {
		fetch('/projects', {
			method: 'POST',
			headers: {
				authorization: `Bearer ${localStorage.getItem('token')}`,
			},
			body: formData,
		})
			.then((res) => {
				if (res.status === 401) {
					dispatch({
						type: 'LOGOUT_USER',
					});
					throw new Error('UN_AUTHORIZED');
				}

				return res.json();
			})
			.then((res) => {
				dispatch({
					type: 'ADD_PROJECT',
					data: res.data,
				});
				dispatch({
					type: 'HIDE_POPUP',
				});
			});
	};
}

export function getProjects() {
	return (dispatch) => {
		fetch('/projects', {
			method: 'GET',
			headers: {
				authorization: `Bearer ${localStorage.getItem('token')}`,
			},
		})
			.then((res) => {
				if (res.status === 204) {
					return { data: [] };
				}
				if (res.status === 401) {
					dispatch({
						type: 'LOGOUT_USER',
					});
					throw new Error('UnAUTHORIZED');
				}

				return res.json();
			})
			.then((res) => {
				dispatch({
					type: 'GET_PROJECTS',
					data: {
						projects: normalize(res.data, 'id'),
					},
				});
			})
			.catch((err) => {
				// console.log(err);
			});
	};
}

export function deleteProject(projectId) {
	return (dispatch) => {
		fetch(`/projects/${projectId}`, {
			method: 'DELETE',
			headers: {
				authorization: `Bearer ${localStorage.getItem('token')}`,
			},
		})
			.then((res) => {
				if (res.status === 401) {
					dispatch({
						type: 'LOGOUT_USER',
					});
					throw new Error('UN_AUTHORIZED');
				}
				return res.json();
			})
			.then((res) => {
				dispatch({
					type: 'DELETE_PROJECT',
					data: {
						projectId,
					},
				});

				dispatch({
					type: 'HIDE_POPUP',
				});
			});
	};
}

export function updateProject(data) {
	return (dispatch) => {
		fetch(`/projects/${data.projectId}`, {
			method: 'PATCH',
			headers: {
				'Content-Type': 'application/json',
				authorization: `Bearer ${localStorage.getItem('token')}`,
			},
			body: JSON.stringify({
				title: data.title,
				section1: data.section1,
				section2: data.section2,
			}),
		})
			.then((res) => {
				if (res.status === 401) {
					dispatch({
						type: 'LOGOUT_USER',
					});
					throw new Error('UN_AUTHORIZED');
				}
				return res.json();
			})
			.then((res) => {
				dispatch({
					type: 'UPDATE_PROJECT',
					data: {
						id: res.data.id,
						data: res.data,
					},
				});
				dispatch({
					type: 'HIDE_POPUP',
				});
			});
	};
}
