import React from 'react';
import { connect } from 'react-redux';

import { login } from '../../../actions/userActions';

import './LoginContainer.css';

function ValidateEmail(mail) {
	if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(mail)) {
		return true;
	}
	return false;
}

class LoginContainer extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			email: '',
			password: '',
			emailValidationMsg: '',
			passwordValidationMsg: '',
		};
		this.handleLogin = this.handleLogin.bind(this);
	}

	handleLogin() {
		let { login } = this.props;
		let { emailValidationMsg, password, passwordValidationMsg, email } = this.state;
		if (email.length > 0) {
			if (password.length > 0) {
				if (!emailValidationMsg && !passwordValidationMsg) {
					login({ email, password });
				}
			} else {
				this.setState({ passwordValidationMsg: "Password can't be Empty" });
			}
		} else {
			this.setState({ emailValidationMsg: "email/username can't be Empty" });
		}
	}

	// handleEmailChange;

	render() {
		let { errMessage } = this.props;
		return (
			<div className="dflex flexcolumn loginContainer">
				<div className="login-title">Brand Name</div>
				<div className="login-form">
					<div className="form-control">
						<label>Username</label>
						<input
							type="email"
							className="form-input"
							value={this.state.email}
							onChange={(e) => {
								this.setState({ email: e.target.value });
								// debugger;
								if (!ValidateEmail(e.target.value)) {
									this.setState({ emailValidationMsg: 'Invalid Email' });
								} else {
									this.setState({ emailValidationMsg: '' });
								}
							}}
						/>
						{this.state.emailValidationMsg ? (
							<div className="verrMsg">{this.state.emailValidationMsg}</div>
						) : null}
					</div>
					<div className="form-control">
						<label>Password</label>
						<input
							type="password"
							className="form-input"
							value={this.state.password}
							onChange={(e) => {
								this.setState({ password: e.target.value });
								if (e.target.value.length <= 0) {
									this.setState({ passwordValidationMsg: "Password can't be Empty" });
								} else {
									this.setState({ passwordValidationMsg: '' });
								}
							}}
						/>
						{this.state.passwordValidationMsg ? (
							<div className="verrMsg">{this.state.passwordValidationMsg}</div>
						) : null}
					</div>
					<div>
						<div className="dflex">
							<span className="form-btn" onClick={this.handleLogin}>
								Login
							</span>
						</div>
					</div>
				</div>
				{errMessage ? <div className="verrMsg pd15">{errMessage}</div> : null}
			</div>
		);
	}
}

export default connect(
	(state, props) => {
		return { errMessage: state.user.errMessage };
	},
	{ login }
)(LoginContainer);
