import React from 'react';
import './ProjectformContainer.css';
import { connect } from 'react-redux';

import { addProject, deleteProject, updateProject } from '../../../actions/projectActions';
import { hidepopupLayer } from '../../../actions/popupLayerActions';
class ProjectFormContainer extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			title: '',
			section1: '',
			section2: '',
			disabled: true,
			type: 'add',
			images: null,
		};
		this.handleSave = this.handleSave.bind(this);
		this.handleUpdate = this.handleUpdate.bind(this);
		this.fileInput = React.createRef();
	}

	handleSave() {
		let { addProject } = this.props;
		let { title, section1, section2 } = this.state;
		if (title && section1 && section2) {
			addProject({
				title,
				section1,
				section2,
				images: this.fileInput.current.files,
			});
		} else {
			this.setState({ errMsg: 'All fileds are mandatory' });
		}
	}

	handleUpdate() {
		let { projectId, updateProject } = this.props;
		let { title, section1, section2 } = this.state;
		if (title && section1 && section2) {
			updateProject({
				projectId,
				title,
				section1,
				section2,
			});
		} else {
			this.setState({ errMsg: 'All fileds are mandatory' });
		}
	}

	componentDidMount() {
		let { projectInfo, type } = this.props;
		if (projectInfo) {
			this.setState({
				title: projectInfo.title,
				section1: projectInfo.section1,
				section2: projectInfo.section2,
				images: projectInfo.images,
			});
		}
		this.setState({ type });
		if (type === 'add' || type === 'edit') {
			this.setState({ disabled: false });
		}
	}

	render() {
		let { projectId } = this.props;
		return (
			<div className="dflex flexcolumn projectFormContainer h100">
				<div className="flexgrow formElements">
					<div className="form-control">
						<label className={this.state.title.length > 0 ? '' : 'required'}>Project Title</label>
						<input
							type="text"
							className="form-input"
							value={this.state.title}
							onChange={(e) => {
								this.setState({ title: e.target.value });
							}}
							disabled={this.state.disabled}
						/>
					</div>
					<div className="form-control">
						<label className={this.state.section1.length > 0 ? '' : 'required'}>Section-1</label>
						<textarea
							rows={5}
							type="text"
							className="form-textarea"
							value={this.state.section1}
							onChange={(e) => {
								this.setState({ section1: e.target.value });
							}}
							disabled={this.state.disabled}
						/>
					</div>
					<div className="form-control">
						<label className={this.state.section2.length > 0 ? '' : 'required'}>Section-2</label>
						<textarea
							rows={5}
							type="text"
							className="form-textarea"
							value={this.state.section2}
							onChange={(e) => {
								this.setState({ section2: e.target.value });
							}}
							disabled={this.state.disabled}
						/>
					</div>
					{this.state.type === 'add' ? (
						<div className="form-control">
							<label>Images</label>
							<input type="file" multiple ref={this.fileInput} />
						</div>
					) : (
						<div className="form-control">
							{this.state.images.map((image) => {
								return (
									<img
										src={`/${image}`}
										height="100px"
										width="100px"
										alt={image}
										style={{ margin: '10px' }}
									/>
								);
							})}
						</div>
					)}
					{this.state.errMsg ? <div className="required">{this.state.errMsg}</div> : null}
				</div>
				<div className="formFooter">
					{this.state.type === 'add' ? (
						<span className="formBtn" onClick={this.handleSave}>
							Save
						</span>
					) : this.state.type === 'edit' ? (
						<span className="formBtn" onClick={this.handleUpdate}>
							Edit
						</span>
					) : this.state.type === 'delete' ? (
						<span
							className="formBtn"
							onClick={() => {
								this.props.deleteProject(projectId);
							}}>
							Delete
						</span>
					) : (
						<span
							className="formBtn"
							onClick={() => {
								this.props.hidepopupLayer();
							}}>
							Close
						</span>
					)}
				</div>
			</div>
		);
	}
}

function mapStateToProps(state, props) {
	return {
		projectInfo: state.projects[props.projectId],
		type: state.popupLayer.type,
	};
}

export default connect(mapStateToProps, { addProject, hidepopupLayer, deleteProject, updateProject })(
	ProjectFormContainer
);
