import React from 'react';
import './Header.css';

export default function Header(props) {
	let { changePasswordHandler, logoutHandler } = props;
	return (
		<div className="dflex headerContainer">
			<div className="flexgrow headerTitle">Site Name</div>
			<div
				onClick={() => {
					changePasswordHandler();
				}}
				className="pointer headerIcon">
				<span className="icon-key"></span>
			</div>
			<div
				onClick={() => {
					logoutHandler();
				}}
				className="pointer headerIcon">
				<span className="icon-exit"></span>
			</div>
		</div>
	);
}
